
import { defineComponent, PropType, CSSProperties } from "vue";

interface Status {
  style: CSSProperties;
}

export default defineComponent({
  name: "input-text-component",
  props: {
    width: {
      type: Object as PropType<CSSProperties["width"]>,
    },
    height: {
      type: Object as PropType<CSSProperties["height"]>,
    },
  },
  setup(props): Status {
    const style = {
      width: props.width,
      height: props.height,
    };

    return {
      style,
    };
  },
});
